import React from "react";
import profile from "../../assets/img/profile.JPG";
import doge from "../../assets/img/doge.png";
import dogeMobile from "../../assets/img/dogeMobile.png";
import cv from "./cv.pdf";
import "./AboutMe.css";

class AboutMe extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      isDesktop: false //This is where I am having problems
    };

    this.updatePredicate = this.updatePredicate.bind(this);
  }

  componentDidMount() {
    this.updatePredicate();
    window.addEventListener("resize", this.updatePredicate);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.updatePredicate);
  }

  updatePredicate() {
    this.setState({ isDesktop: window.innerWidth > 768 });
  }

  render() {

    const isDesktop = this.state.isDesktop;

    return (
      <div className="container">
        <div className="img-wrapper">
          <div className="profile-img-wrapper">
            <img className="profile-img" src={profile} />
          </div>
          <div className="bio-info-wrapper">
            <p className="bio-info">
              <b>Name</b>: Darryl Leong
            </p>
            <p className="bio-info">
              <b>Major</b>: Information Systems
            </p>
            <p className="bio-info">
              <b>School</b>: National University of Singapore
            </p>
            <div className="bio-info-doge">
              <div className="loves">
                <b>Loves</b>: Running, Music, Cheese
              </div>
              <a
                className="doge-wrapper bounce"
                href="https://www.instagram.com/hunt_solo_/"
                target="_blank"
              >
                <img className="hunt-insta" src={ isDesktop ? doge: dogeMobile} />
              </a>
            </div>
          </div>
        </div>
        <div className="about-me-wrapper">
          <div className="section-header">
            <h2 className="section-header-text">
              <span>HEY EVERYONE</span>
            </h2>
          </div>
          <div className="section-body-wrapper">
            <div className="section-body">
              <p>
                Nice to meet ya, my name is{" "}
                <span className="bolded-words">Darryl Leong</span>. I'm
                currently finishing up my last year in college at the{" "}
                <span className="bolded-words">
                  National University of Singapore
                </span>
                . I really enjoy building and working on my own projects,
                whether it is to serve a functional purpose or explore, I'm
                always keen to learn something new.
              </p>

              <p>
                When it comes to tech, I find myself invested in ideas which are
                able to directly impact the lives of others. Though I don't
                particularly have a preference towards any stack/technology, I'm
                very much a believer in taking the "shortest path" possible
                while balancing my goals of learning new technologies.
              </p>

              <p>
                You might've randomly stumbled on this website but now that
                you're here I hope you take your time and enjoy it!
              </p>
            </div>
          </div>
          <div className="cv-wrapper">
            <div className="cv-button-wrapper">
              <a href={cv} download="DARRYL_LEONG_CV" className="cv-link">
                DOWNLOAD MY CV
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default AboutMe;
